import Vue from 'vue'
import Router from 'vue-router'
import Hv from 'components/Hv/Hv'
import PersonHFive from 'components/personHFive/personHFive'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'Hv',
      component: Hv
    },
    {
      path: '/Hv',
      name: 'Hv',
      component: Hv
    },
    {
      path: '/personHFive',
      name: 'personHFive',
      component: PersonHFive
    }
  ]
})



