<template>
  <div class="Hv" ref="hv" v-wechat-title="$route.meta.title=pname">
    <div class="bgImg" :style="`background-image:url(${img})`"></div>
    <div class="groupChan">
      <div class="tab">
        <div class="radius-header">
          <div class="radius-word">
            <span class="radius-sp1">已有</span>
            <span class="radius-red">10075</span>
            <span class="radius-sp1">人参加</span>
          </div>
          <div class="radius-avatar">
            <!--<img :src="item.url" v-for="(item, index) in img" :key="index"/>-->
            <img src="../../assets/1@3x.png" />
            <img src="../../assets/2@3x.png" />
            <img src="../../assets/3@3x.png" />
            <img src="../../assets/4@3x.png" />
            <img src="../../assets/5@3x.png" />
            <img src="../../assets/6@3x.png" />
            <img src="../../assets/more.png" />
          </div>
        </div>
        <ul class="tab-ul clearfix">
          <li
            :class="{showli:index === ins}"
            v-for="(item, index) in list"
            :key="index"
            @click="tab(index, item)">{{item.remarks}}</li>
        </ul>
        <div class="leftCricle"></div>
        <div class="rightCricle"></div>
      </div>
      <div class="yinDao">
        <div v-if="showCon === false">
          <img src="../../assets/zhiDao.png" />
        </div>
        <div v-else>
          <div v-if="showWeChat === true">
            <div class="loading" v-if="loading === false">
              <!--<div class="loadingBg"></div>-->
              <!--<img src="../../assets/loadingthree.gif" />-->
              <img src="../../assets/loadingfour.gif" />
            </div>
            <div v-else>
              <div class="reason">{{task.recommendedReasonsList[0].content}}</div>
              <img :src="no.qrCode" />
              <div class="safe"><i class="el-icon-success"></i>此二维码已经过安全认证，请放心扫描</div>
            </div>
          </div>
          <div class="taskEnd" v-else>此任务已经结束，请参加其他任务</div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import {apiPages, ERR_OK} from 'api/config'
import {getCookie} from '../../api/cookie'

export default {
  name: 'Hv',
  data () {
    return {
      showWeChat: true, // 是否有个人号
      passageId: '',
      flag: false,
      arr: [], // menu列表
      clientHeight: '', // 动态高度
      show: true, // 动态展示群二维码
      id: '', // 地址链接
      data: [],
      srcImg: '',
      ins: null,
      nullVal: false,
      pname: '',
      list: [],
      img: '../../assets/gscl.jpg',
      showCon: false,
      roadId: '',
      response: [],
      no: [],
      task: [],
      loading: true
    }
  },
  created () {
    this.$route.meta.title = this.pname
    console.log(this.$route.meta.title)
    // 获取浏览器可视区域高度
    this.clientHeight = `${document.documentElement.clientHeight}` // document.body.clientWidth;
    console.log(this.clientHeight)
    window.onresize = function temp () {
      this.clientHeight = `${document.documentElement.clientHeight}`
    }
    let cookies = getCookie('PersonalNoUserInfoSuff')
    this.response = JSON.parse(cookies)
    this.roadId = this.response.roadId
    this.$http.get(`${apiPages}/personal/noTask/getByRoadId/${this.response.roadId}/`).then((response) => {
      response = response.body
      console.log(response)
      if (response.code === ERR_OK) {
        this.$message({
          type: 'error',
          message: '数据丢失了哦，请重试！！'
        })
      } else {
        this.list = response.data.taskList
        this.img = response.data.road.roadBackground
        this.pname = response.data.taskList[0].taskTheme
      }
    })
  },
  methods: {
    // 动态修改样式
    changeFixed (clientHeight) {
      console.log(clientHeight)
      this.$refs.hv.style.height = clientHeight + 'px'
    },
    // li
    tab (res, item) {
      this.loading = false
      this.showCon = true
      this.show = !this.show
      this.ins = res
      console.log(item)
      var param = {
        nickName: this.response.nickName,
        openId: this.response.openId,
        taskId: item.id,
        unionId: this.response.unionId
      }
      this.$http.post(`${apiPages}/personal/noTask/getPersonalByTaskId`, param).then((response) => {
        console.log(response)
        console.log(response.body.data.wechat)
        if (response.body.code === ERR_OK) {
          this.$message({
            type: 'error',
            message: response.body.message
          })
        } else {
          this.loading = true
          if (response.body.data.wechat === null || response.body.data.wechat === '') {
            this.showWeChat = false
            var taskEndCssStyle = document.querySelector('.taskEnd')
            taskEndCssStyle.style.height = '3rem'
            taskEndCssStyle.style.lineHeight = '3rem'
          } else {
            this.showWeChat = true
            this.no = response.body.data.wechat
            this.task = response.body.data.task
          }
        }
      })
    }
  },
  mounted () {},
  watch: {
    // 如果 `clientHeight` 发生改变，这个函数就会运行
    clientHeight: function () {
      this.changeFixed(this.clientHeight)
    }
  }
}
</script>

<style lang="stylus" rel="stylesheet/stylus">
  @import "../../api/mixin.styl"   //引用mixin.styl文件
  * {margin: 0;padding: 0px;border: none;}
  .clearfix:after{
    content: "020";
    display: block;
    height: 0;
    clear: both;
    visibility: hidden;
  }
  .clearfix {
    /* 触发 hasLayout */
    zoom: 1;
  }
  .Hv {
    width: 100%;
    background: #fbf8e5;
    background-size: 100% 100%;
    position: fixed;
    overflow-x: hidden;
    overflow-y: auto;}
  .tab-ul {
    width: 100%;
    overflow: hidden;
    padding: 1rem 0.6rem 0.5rem 0.6rem;
    -webkit-box-sizing: border-box;
    box-sizing: border-box;
    padding-top: 2.5rem;
  }
  .tab-ul li {
    width: 28%;
    float: left;
    text-align: center;
    background-color: #ebebeb;
    color: #000000;
    list-style: none;
    font-size: 0.3rem;
    margin-right: 8%;
    height: 0.6rem;
    line-height: 0.6rem;
    margin-bottom: 0.2rem;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
  }
  .tab-ul li:nth-child(2n+1) {
    /*margin-right: 0;*/
  }
  .tab-ul li:nth-child(1) {
    margin-right: 8%;
  }
  .tab-ul li:nth-child(3) {
    margin-right: 0rem;
  }
  .tab-ul li:nth-child(6) {
    margin-right: 0rem;
  }
  .tab-ul li.active {
    background-color: #fae579;
    color: #000;
  }
  .shows span.bgora {
    background: #fae579;
    margin: 20px auto 0;
    border-radius: 10px;
    height: 40px;
    line-height: 40px;
    font-size: 16px;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
    padding: 0px 10px;
    box-sizing: border-box;
  }
  .MaShow .shows .sp1 {
    font-size: 14px;
    background: #f0feef;
    color: #64bf56;
  }
  .tab-ul .showli {
    color: #fff;
    background: #ff9100;
  }
  .opacity {
    position: absolute;
    width: 100%;
    height: 100%;
    z-idnex: 10
  }
  .opacity img {
    width: 100%;
  }
  .bgImg {
    width: 100%;
    height: 4rem;
    background-size: 100% 100%;
  }
  .groupChan {
    position: absolute;
    padding: 0rem 0.2rem;
    -webkit-box-sizing: border-box;
    box-sizing: border-box;
    top: 1.5rem;
    width: 100%;
    /*overflow-x: hidden;
    overflow-y: auto;*/
  }
  .tab {
    position: relative;
    background-color: #fff;
    border-top-left-radius: 4rem;
    border-top-right-radius: 4rem;
    border-bottom: 0.03rem dashed #cccccc;
  }
  .radius-header {
    width: 100%;
    display: flex;
    flex-direction: column
  }
  .radius-word {
    width: 100%;
    display: flex;
    flex-direction: row;
    position: absolute;
    left: 50%;
    margin-left: -16%;
    top: 0.4rem;
    line-height: 0.5rem;
  }
  .radius-avatar {
    width: 100%;
    display: flex;
    flex-direction: row;
    position: absolute;
    top: 1.3rem;
    left: 50%;
    margin-left: -38%;
  }
  .radius-avatar img {
    width: 0.7rem;
    height: 0.7rem;
    border-radius: 50%;
    margin-right: 0.1rem;
  }
  .radius-sp1 {
    color: #a0a7b2;
    font-size: 0.3rem;
  }
  .radius-red {
    color: #fa5c32;
    font-weight: bold;
    font-size: 0.4rem;
  }
  .leftCricle {
    position: absolute;
    width: 30px;
    height: 25px;
    background: #fdf8e4;
    z-index: 10;
    border-radius: 20px;
    left: -15px;
    bottom: -12px;
  }
  .rightCricle {
    position: absolute;
    width: 30px;
    height: 25px;
    background: #fdf8e4;
    z-index: 10;
    border-radius: 20px;
    right: -15px;
    bottom: -12px;
  }
  .safe {
    width: 100%;
    height: 0.6rem;
    line-height: 0.6rem;
    text-align: center;
    font-size: 0.25rem;
    background: #f0feef;
    color: #64bf56;
    margin-bottom: 0.4rem;
  }
  .reason {
    width: 100%;
    height: 0.6rem;
    margin-top: 0.2rem;
    line-height: 0.6rem;
    font-size: 0.25rem;
    text-align: center;
  }
  .yinDao {
    width: 100%;
    background-color: #ffffff;
    text-align: center;
    display: flex;
    flex-direction:column;
  }
  .yinDao img {
    width: 3rem;
    margin: 0rem auto;
  }
  .loading {
    width: 1rem;
    height: 1rem;
    margin: 0 auto;
    line-height: 3.5rem;
    position: relative
    margin: 0.2rem auto;
  }
  .loadingBg {
    width: 3rem;
    height: 3rem;
    background: #ffffff;
    opacity:0.8;
    position: absolute
  }
  .loading img {
    width: 100%;
    /*height: 3rem;*/
    z-index: 999;
    display: block;
    position: absolute;
    left: 50%;
    margin-left: -50%;
    top: -4%;
  }
  .taskEnd {
    font-size: 0.3rem;
    color: red;
    font-weight: bold;
    text-align: center;
    padding: 20px 0px;
  }
  .groupChan::-webkit-scrollbar {
    /*滚动条整体样式*/
    width: 10px; /*高宽分别对应横竖滚动条的尺寸*/
    height: 1px;
  }
  .groupChan::-webkit-scrollbar-thumb {
    /*滚动条里面小方块样式*/
    border-radius: 10px;
    -webkit-box-shadow: inset 0 0 5px rgba(0,0,0,0.2);
    background: #535353;
  }
  .groupChan::-webkit-scrollbar-track {
    /*滚动条里面轨道样式*/
    webkit-box-shadow: inset 0 0 5px rgba(0,0,0,0.2);
    border-radius: 10px;
    background: #EDEDED;
  }
</style>



